*{
    list-style-type: none;
    font-family: 'Exo', sans-serif;
}
a{
    text-decoration:underline;
    color:aquamarine;
}
a:hover{
    text-decoration:none; 
    font-size:110%;
}
.searchBar  > #search {
    width:50%;
    color:ghostwhite;
    background-color: rgb(27, 25, 25);
    text-align:center;
    height:40px;
    border-radius: 8px;
    margin:30px 25% 20px 25%;
    border:none;
    font-size: 130%;
}
.userDataContainer{
    width:50%;
    background-color: #332f2f;
    height: auto;
    margin-left:auto;
    margin-right:auto;
    padding-bottom:6px;
}
.section1{
    height:auto;
    /* padding-bottom: 5px; */
    display: flex;
    justify-content: space-evenly;
} 
.avatar{
    margin-top: 10px;
    border-radius: 50%;
    width:200px;
    height: 200px;
}
.primaryInfo{
    /* margin-top:30px; */
    list-style-type: none;
}
.primaryInfo li{
    padding: 3px;
}
.bio{
    height:auto;
margin: 30px;
}
.social{
    background-color: #1e1e1e;
    margin: 5px;
    border-radius: 8px;
    margin-top:30px;
    height:60px;
    width:90%;
    margin-left:auto;
    margin-right: auto;
    text-align: center;;
    display:flex;
    justify-content: space-around;
    align-items: center;
}
.addrSection{
    margin:30px;
}
.addrSection li{
    padding:3px;
}


/* MEDIA QUERIES */


@media (max-width:1200px) and (min-width:780px) {
    .userDataContainer{
        width:60%;
    }
}
@media (max-width:779px) and (min-width:660px) {
    .userDataContainer{
        width:70%;
    }
}
@media (max-width:660px) and (min-width:580px) {
    .userDataContainer{
        width:80%;
    }
}
@media (max-width:580px) and (min-width:540px) {
    .userDataContainer{
        width:90%;
    }
}
@media (max-width:540px){
    .userDataContainer{
        width:100%;
    }
   .searchBar > #search{
    height:30px;
    font-size: 14px;
    }
    .section1{
        flex-wrap: wrap;
    }
}